import { Component, OnInit, } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { CommonService } from 'src/app/service/common.service';
import { CarrierService } from 'src/app/service/carrier.service';
declare var document: any;

@Component({
  selector: 'app-add-carrier',
  templateUrl: './add-carrier.component.html'
})
export class AddCarrierComponent implements OnInit {

  PageTitle = "Carrier"

  loadingState = false;
  addForm: FormGroup;
  showLoader = false;
  formErrors = {
    carrier_code: null,
    apierror: null,
  };
  isEditing = false;
  editId = null;

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private carrierService: CarrierService,
    private commonservice: CommonService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
    }

    this.addForm = this.fb.group({
      carrier_name: [null, Validators.compose([Validators.required])],
      carrier_code: [null, Validators.compose([Validators.required])],
      contact_person: [null, Validators.compose([Validators.required])],
      contact_no: [null, Validators.compose([Validators.required])],

    });
  }

  ngOnInit() {
    if (this.isEditing) {
      this.getEditObject()
    }
  }





  getEditObject() {
    this.carrierService.getCarrierById(this.editId).subscribe((response) => {
      if (response.success) {
        this.addForm.patchValue(response.data);
      } else {
        this.router.navigateByUrl('/manage/Carrier');
      }
    });
  }

  submitForm(formData: any): void {
    if (formData.valid) {
      this.showLoader = true;
      const data = new FormData();
      data.append('carrier_name', formData.value.carrier_name);
      data.append('carrier_code', formData.value.carrier_code);
      data.append('contact_person', formData.value.contact_person);
      data.append('contact_no', formData.value.contact_no);

      if (this.isEditing) {
        this.carrierService.editCarrier(this.editId, data).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/manage/carrier');
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('carrier_code')) {
                this.formErrors['carrier_code'] = obj['carrier_code'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });
      } else {
        this.carrierService.addCarrier(data).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/manage/carrier');
          } else {
            response.error.map(obj => {
              if (obj.hasOwnProperty('carrier_code')) {
                this.formErrors['carrier_code'] = obj['carrier_code'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });
      }
    }
  }
}
