import { Component, OnInit } from '@angular/core';
import { PaginationService } from 'src/app/service/pagination.service';
import { TextField, ActionField } from 'src/app/common-module/table-list/list-field-type';
import { CarrierService } from 'src/app/service/carrier.service';

@Component({
  selector: 'app-carrier-list',
  templateUrl: './carrier-list.component.html'
})
export class CarrierListComponent implements OnInit {

  PageTitle = "Manage Carrier";

  action: any;
  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  permissionObject: any = null;
  showPagination: boolean = false;
  //For Dynamic List
  headerData: Array<any> = [];

  constructor(
    private paginationService: PaginationService,
    private carrierService: CarrierService,
  ) { }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['self'];
    // });
    this.loadform = false;
    this.setHeaderData();
    this.getObjects();
  }
  setHeaderData() {
    this.headerData.push(
      new TextField({
        label: "Carrier Name",
        key: "carrier_name",
      }),
      new TextField({
        label: "Carrier Code",
        key: "carrier_code",
      }),

      new TextField({
        label: "Contact Person",
        key: "contact_person",
      }),
      new TextField({
        label: "Contact No",
        key: "contact_no",
      }),

    );
    let canEdit = true;
    let canDelete = true;
    let canView = true;
    let actions = {};
    if (canEdit) {
      actions['edit'] = {
        path: '/manage/carrier/edit/',
        id: 'carrier_id'
      }
    }
    // if (canView) {
    //   actions['view'] = {
    //     path: '/manage/carrier/view/',
    //     id: 'carrier_id'
    //   }
    // }
    if (canDelete) {
      actions['delete'] = {
        path: 'carrier/',
        id: 'carrier_id'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }

  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.carrierService.getCarrierList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {

        this.objectArray = response.data.list;
        this.showPagination = true;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }
  onReloadEvent() {
    this.getObjects();
  }
}




