import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { SharedModule } from 'src/app/shared/shared.module';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { NgSelectModule } from '@ng-select/ng-select';
import { CarrierViewComponent } from './carrier-view/carrier-view.component';
import { CarrierListComponent } from './carrier-list/carrier-list.component';
import { AddCarrierComponent } from './add-carrier/add-carrier.component';
import { CarrierService } from 'src/app/service/carrier.service';


const routes: Routes = [
  {
    path: "",
    component: CarrierListComponent,
    data: { title: 'carrier_list' }
  },
  {
    path: "add",
    component: AddCarrierComponent,
    data: { title: 'add_carrier' }
  },
  {
    path: 'edit/:id',
    component: AddCarrierComponent,
    data: { title: 'edit_carrier' }
  },
  // {
  //   path: 'view/:id',
  //   component: CarrierViewComponent,
  //   data: { title: 'view_carrier' }
  // },
]

@NgModule({
  declarations: [AddCarrierComponent, CarrierListComponent, CarrierViewComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TableListModule,
    SearchModule,
    BtnAddModule,
    FormsModule,
    ReactiveFormsModule,
    FormValidationModule,
    NgSelectModule,
    SharedModule,
    PaginationModule
  ], providers: [CarrierService]
})
export class CarrierModule { }
